enter;\n\n  &:focus {\n    left: 5px;\n  }\n}\n\n// * Navigation Bar\nheader {\n  background-color: white;\n  position: sticky;\n  top: 0;\n  z-index: 100;\n}\nheader.scrolled {\n  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);\n  transition: all 0.3s ease-in-out;\n}\n\n#hamburger {\n  display: block;\n  position: relative;\n  z-index: 100;\n  min-width: 44px;\n  min-height: 44px;\n  font-size: 32px;\n  border: none;\n  background-color: transparent;\n  color: $primary;\n  cursor: pointer;\n}\n\n.nav_container {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  .nav_brand {\n    z-index: 100;\n    .logo_wrapper {\n      height: 44px;\n      display: inline-block;\n      #logo {\n        height: 40px;\n        width: auto;\n        object-fit: contain;\n      }\n    }\n  }\n}\n\n.nav_content {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 60px;\n  .nav_list {\n    list-style: none;\n    display: none;\n    flex-direction: column;\n    gap: 8px;\n    justify-content: center;\n    align-items: start;\n    top: 0;\n    right: 0;\n    background-color: white;\n    height: 100vh;\n    width: 50%;\n    margin: auto;\n    a {\n      min-height: 44px;\n      min-width: 44px;\n      display: flex;\n      padding: 8px 28px;\n      margin: 0 auto;\n      align-items: center;\n      justify-content: start;\n      text-decoration: none;\n      background-color: white;\n      font-weight: $medium;\n      color: $primary;\n      &:hover {\n        color: $tertiary;\n      }\n    }\n    animation: fade 0.3s ease-in-out;\n    @keyframes fade {\n      from {\n        opacity: 0;\n        transform: translate(50vw, 0);\n      }\n      to {\n        opacity: 1;\n        transform: translate(0, 0);\n      }\n    }\n  }\n  .nav_list.open {\n    position: absolute;\n    display: flex;\n  }\n}\n// * Navigation Bar End\n\n// * Main Content\nmain {\n  margin: 0 auto;\n  min-height: 100vh;\n}\n\nmain.blur {\n  filter: blur(4px);\n  transition: all 0.3s ease-in-out;\n}\n\n.hero_container {\n  position: relative;\n  height: 320px;\n  img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: -100;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    filter: brightness(0.55);\n  }\n\n  .hero_content {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    height: 100%;\n    color: white;\n    gap: 8px;\n    text-align: center;\n    h1 {\n      text-transform: uppercase;\n      font-size: 32px;\n    }\n    p {\n      width: 90%;\n      font-size: 14px;\n      line-height: 1.5rem;\n    }\n  }\n}\n\n.resto_container {\n  padding: 36px 20px;\n  .resto_title {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 8px;\n    margin-bottom: 28px;\n    text-align: center;\n    h1,h2 {\n      font-size: 24px;\n    }\n    p {\n      font-size: 14px;\n    }\n  }\n}\n\n.resto_list,\n.search-list {\n  display: grid;\n  grid-template-columns: repeat(1, minmax(0, 1fr));\n  gap: 20px;\n}\n\n.resto_item {\n  filter: drop-shadow(0 10px 8px rgb(0 0 0 / 0.04)) drop-shadow(0 4px 3px rgb(0 0 0 / 0.1));\n  background-color: white;\n  border-radius: 0.5rem;\n  overflow: hidden;\n  transition: all 0.2s ease-in-out;\n  &:hover {\n    transform: translate(0, -4px);\n  }\n  .resto_image_wrapper {\n    position: relative;\n    height: 200px;\n    overflow: hidden;\n    img {\n      width: 100%;\n      height: 100%;\n      object-fit: cover;\n      position: absolute;\n      bottom: 0;\n      left: 0;\n      z-index: -10;\n      border-radius: 0.5rem 0.5rem 0 0;\n      transition: all 0.3s ease-in-out;\n    }\n    &:hover img {\n      transform: scale(1.05);\n    }\n    p {\n      position: absolute;\n      top: 0;\n      left: 0;\n      padding: 8px 16px;\n      background-color: rgba(0, 0, 0, 0.6);\n      color: white;\n      font-size: 18px;\n      font-weight: $medium;\n      border-radius: 0.5rem 0 0.5rem 0;\n      cursor: default;\n    }\n  }\n  .resto_content {\n    padding: 0 20px 12px 20px;\n    .title {\n      display: flex;\n      justify-content: space-between;\n      align-items: center;\n      margin: 12px 0;\n      a {\n        min-height: 44px;\n        min-width: 44px;\n        text-decoration: none;\n        color: $primary;\n        font-size: 20px;\n        padding: 12px 8px 12px 0;\n        font-weight: $semibold;\n        transition: all 0.2s ease-in-out;\n\n        &:hover {\n          color: $tertiary;\n        }\n      }\n      .rating {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        gap: 4px;\n        font-weight: $medium;\n        color: $secondary;\n        cursor: default;\n      }\n    }\n    .desc {\n      font-size: 14px;\n      line-height: 1.3rem;\n      color: $primary;\n      text-align: justify;\n      cursor: default;\n      overflow: hidden;\n      display: -webkit-box;\n      -webkit-line-clamp: 5;\n      -webkit-box-orient: vertical;\n    }\n  }\n}\n\n.details-container {\n  padding: 20px 28px;\n  display: flex;\n  flex-direction: column;\n  gap: 32px;\n}\n.details-title {\n  font-size: 24px;\n  color: $primary;\n  span {\n    font-size: 18px;\n    color: $secondary;\n    font-weight: $regular;\n  }\n}\n.details-subtitle {\n  font-size: 20px;\n  color: $primary;\n  text-align: center;\n}\n.details-subheader {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  justify-content: center;\n  gap: 20px;\n}\n.details-image {\n  width: 100%;\n  height: 400px;\n  object-fit: cover;\n  border-radius: 12px;\n}\n.details-content {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  gap: 8px;\n}\n.details-content-item {\n  display: flex;\n  .details-content-title {\n    width: 80px;\n    font-weight: $medium;\n  }\n}\n.details-description {\n  text-align: justify;\n  font-size: 14px;\n}\n.details-menu {\n  display: flex;\n  justify-content: center;\n  align-items: start;\n  gap: 10px;\n  padding-top: 12px;\n}\n.details-menu-item {\n  width: 50%;\n  height: 100%;\n  border: 1px solid $quaternary;\n  border-radius: 8px;\n  text-align: center;\n  padding: 10px;\n\n  .details-menu-title {\n    color: $primary;\n    font-size: 16px;\n    font-weight: $semibold;\n    padding-bottom: 8px;\n  }\n  .details-menu-wrapper {\n    display: grid;\n    grid-template-columns: repeat(1, minmax(0, 1fr));\n    gap: 6px;\n    .details-menu-description {\n      font-size: 14px;\n      background-color: #eff2f8;\n      padding: 6px 2px;\n      border-radius: 4px;\n    }\n  }\n}\n.details-review-wrapper {\n  display: grid;\n  grid-template-columns: repeat(1, minmax(0, 1fr));\n  row-gap: 8px;\n  column-gap: 12px;\n  color: $secondary;\n  padding-top: 12px;\n\n  .details-review-item {\n    display: flex;\n    flex-direction: column;\n    justify-content: start;\n    border: 1px solid $tertiary;\n    border-radius: 8px;\n    padding: 12px;\n    gap: 4px;\n    .review-item-header {\n      display: flex;\n      justify-content: space-between;\n      align-items: center;\n      .review-item-user {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        gap: 4px;\n        h4 {\n          font-weight: $semibold;\n        }\n      }\n      .review-item-date {\n        color: $secondary;\n        font-size: 14px;\n      }\n    }\n    .review-item-desc {\n      border-top: 1px solid $tertiary;\n      padding-top: 10px;\n      font-size: 14px;\n    }\n  }\n}\n\n.icons {\n  height: 24px;\n  width: 24px;\n}\n.divider-my2 {\n  height: 1px;\n  width: 100%;\n  background-color: $primary;\n  margin: 20px 0;\n}\n.divider-my1 {\n  height: 1px;\n  width: 100%;\n  background-color: $primary;\n  margin: 10px 0;\n}\n.divider {\n  height: 1px;\n  width: 100%;\n  background-color: $primary;\n}\n\n.form-review {\n  display: flex;\n  flex-direction: column;\n  width: 80%;\n  margin: 0 auto;\n  border: 1px solid $tertiary;\n  border-radius: 8px;\n  padding: 20px;\n  gap: 8px;\n\n  div {\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n  }\n  label {\n    font-size: 14px;\n    color: $primary;\n  }\n  input,\n  textarea {\n    border: 1px solid $tertiary;\n    border-radius: 8px;\n    padding: 16px;\n    font-size: 14px;\n    color: $primary;\n  }\n  button {\n    border: 1px solid $tertiary;\n    border-radius: 8px;\n    padding: 16px;\n    font-size: 14px;\n    color: $quaternary;\n    background-color: $secondary;\n    cursor: pointer;\n    transition: all 0.2s ease-in-out;\n    &:hover {\n      background-color: $tertiary;\n    }\n  }\n}\n.search-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 20px;\n  padding-bottom: 36px;\n}\n.search-content {\n  padding-top: 12px;\n  text-align: center;\n  h2 {\n    font-size: 20px;\n    padding-bottom: 4px;\n  }\n  p {\n    font-size: 14px;\n  }\n}\n.search-list-wrapper {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 20px;\n  width: 100%;\n}\n.search-input-wrapper {\n  width: 80%;\n  position: relative;\n  margin: 0 auto;\n  .search-input {\n    width: 100%;\n    box-sizing: border-box;\n    border: 1px solid $tertiary;\n    border-radius: 8px;\n    padding: 16px;\n    font-size: 14px;\n    color: $primary;\n    &:focus {\n      outline: none;\n    }\n  }\n  .search-button {\n    position: absolute;\n    right: 0;\n    top: 0;\n    border: 1px solid $tertiary;\n    border-radius: 8px;\n    padding: 16px;\n    background-color: $secondary;\n    color: $quaternary;\n    cursor: pointer;\n    transition: all 0.2s ease-in-out;\n    &:hover {\n      background-color: $tertiary;\n    }\n  }\n}\n.card-seach-item {\n  background-color: white;\n  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));\n  border-radius: 12px;\n  \n  .card-search-item-container {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    text-decoration: none;\n    color: $primary;\n    padding: 20px;\n    h1 {\n      display: block;\n      font-size: 18px;\n      font-weight: $semibold;\n      span {\n        font-size: 14px;\n        font-weight: $medium;\n      }\n    }\n    h2{\n      display: block;\n      font-size: 16px;\n      font-weight: $medium;\n      color: $secondary;\n    }\n  }\n}\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: $primary;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n#favorite-none{\n  text-align: center;\n  font-size: 20px;\n  font-weight: $semibold;\n}\n#search-none{\n  text-align: center;\n  font-weight: $semibold;\n}\n#page-none{\n  text-align: center;\n  font-weight: $semibold;\n  padding-top: 30vh;\n}\n.loader-wrapper{\n  width: 100%;\n  height: 300px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.loading-row-wrapper{\n  width: 100%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n.display-none{\n  display: none;\n}\n.lds-roller {\n  display: inline-block;\n  position: relative;\n  width: 80px;\n  height: 80px;\n}\n.lds-roller div {\n  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\n  transform-origin: 40px 40px;\n}\n.lds-roller div:after {\n  content: \" \";\n  display: block;\n  position: absolute;\n  width: 7px;\n  height: 7px;\n  border-radius: 50%;\n  background: $primary;\n  margin: -4px 0 0 -4px;\n}\n.lds-roller div:nth-child(1) {\n  animation-delay: -0.036s;\n}\n.lds-roller div:nth-child(1):after {\n  top: 63px;\n  left: 63px;\n}\n.lds-roller div:nth-child(2) {\n  animation-delay: -0.072s;\n}\n.lds-roller div:nth-child(2):after {\n  top: 68px;\n  left: 56px;\n}\n.lds-roller div:nth-child(3) {\n  animation-delay: -0.108s;\n}\n.lds-roller div:nth-child(3):after {\n  top: 71